@define-mixin autoCenter $width:1000px {
	margin-left: auto;
	margin-right: auto;
	width: $width;
}
.ziliao {
	.wrap {
		@mixin autoCenter;
		height: 100px;

		ul.nav {
			list-style: none;
			width: 100%;
			li {
				list-style: none;
				width: 20%;
				height: 100%;
				/*border: 1px solid red;*/
				float: left;

				a {
					text-decoration: none;
					color: #999;
				}
				a:hover {
					color: #333;
					text-decoration: underline;
				}
			}
		}

	}
}
