.hero {
	width: 1920px;
	min-width: 1005px;

	background: #101010 url(../img/hero/bg.jpg) no-repeat top center;
	.wrap {
		width: 1005px;
		margin-left: -502px;
		position: absolute;
		left: 50%;
		border: 1px solid red;
		.main {
			width: 1170px; 
			height: 600px;
			position: absolute;
			/*left: -75px;*/
			left: -110px;
			top: 140px;
			.title {
				border: 1px solid red;
				width: 400px;
				height: 135px;
				position: absolute;
				top: 0px;
				left: 0px;
				li {
					cursor: pointer;
					z-index: 20;
					/*border: 1px solid red;*/
					position: absolute;
					top: 0px;
					width: 116px;
					height: 132px;
					background: url(../img/hero/title.png) no-repeat 0 0;
				}
				li.tuji {
					left: 18px;
				}
				li.yuancheng {
					left: 145px;
				}
				li.fangyu {
					left: 273px;
				}
			}
			.contents {
				width: 100%;
				height: 650px;
				position: absolute;
				top: 80px;
				border: 1px solid red;

				.list_nav {
					width: 419px;
					height: 487px;
					background: url(../img/hero/listbg.png);
					position: absolute;
					top: 80px;
					left: 0px;
					/*img.gif {
						width: 128px;
						height: 129px;
						display: block;
						position: absolute;
						top: 8px;
						left: 135px;
					}*/
					#list {
						.heros {
							position: absolute;
							top: 155px;
							left: 40px;
							width: 343px;
							height: 285px;
							border: 1px solid red;
							li {
								position: relative;
								width: 78px;
								height: 78px;
								/*display: none;*/
								float: left;
								margin-left: 7px;
								margin-bottom: 15px;
								cursor: pointer;
								img {
									width: 78px;
									height: 78px;
									display: block;
								}
								span.shadow {
									width: 78px;
									height: 78px;
									display: inline-block;
									background: rgba(255,255,255,0.5);
									position: absolute;
									top: 0;
									left: 0;
								}
							}
						}
						.page_control {
							width: 185px;
							height: 40px;
							border: 1px solid red;
							position: absolute;
							top: 435px;
							left: 110px;
							#next ,#prev{
								z-index: 11;
								width: 50%;
								height: 100%;
								cursor: pointer;
								position: absolute;
								top: 0;
								border: 1px solid red;
							}
							#prev {
								left: 0;
							}
							#next {
								left: 93px;
							}

						}
					}
				}
				.heros_contents {
					width: 695px;
					height: 650px;
					border: 1px solid green;
					position: absolute;
					top: 0;
					left: 475px;
