						width: 100%;
						height: 100%;
						position: absolute;
						top: 0px;
						left: 0px;
						/*border: 1px solid red;*/
						
						img.gif {
							width: 128px;
							height: 129px;
							display: block;
							position: absolute;
							top: 90px;
							left: -340px;
						}
						.qiyuan {
							z-index: 10;
							position: absolute;
							top: 0;
							left: 0;
							width: 85px;
							height: 420px;
							background: url(../img/hero/qybg.png) no-repeat 0 0;
							.img {
								width: 240px;
								height: 221px;
								position: absolute;
								top: 170px;
								left: 10px;
								border: 1px solid red;
								div {
									/*border: 1px solid green;*/
									width: 100%;
									margin-top: 10px;
									/*position: absolute;
									top: 10px;
									left: 0px;*/
									img {
										width: 61px;
										height: 61px;
										display: inline-block;
									}
									span {
										color: #d4cbaf;
										font-size: 14px;
										position: absolute;
										min-height: 105px;
										/*display: none;*/
										width: 148px;
										/*padding: 10px;*/
										padding: 10px 15px;
										border: 1px solid red;
										background:  url(../img/hero/qyword.png) no-repeat 0 0;
									}
								}
								/*div:nth-child(1) {
									top: 180px;
								}*/

								/*margin-top: 10px;
								margin-left: auto;
								margin-right: auto;*/
							}
							
						}

						img.hero_img {
							display: block;
							width: 537px;
							height: 728px;
							position: absolute;
							top: -100px;
							left: 0px;
						}
						.star {
							border: 1px solid red;
							padding: 0px 20px;
							position: absolute;
							bottom: 20px;
							left: 70px;
							overflow: hidden;
							background-color: #0b0b0b;
							/*background-image: url(../img/hero/starbgleft.png),url(../img/hero/starbg.png),url(../img/hero/starbg.png),url(../img/hero/starbgright.png);
							background-repeat: no-repeat,repeat,repeat, no-repeat;
							background-position: center left,center center,center center, center right;*/
							li {
								display: inline-block;
								float: left;
							}
						}
						.jineng {
							z-index: 10;
							width: 205px;
							position: absolute;
							top: 25px;
							left: 490px;
							border: 1px solid red;
							li {
								margin-top: 7px;
								overflow: hidden;
								img {
									display: inline-block;
									width: 66px;
									height: 66px;
									float: left;

								}
								span {
									/*padding:5px;*/
									padding-left: 5px;
									line-height: 52px;
									color: #e7e7e7;
									font-size: 16px;
									display: inline-block;
									width: 133px;
									height: 52px;
									float: left;
									margin-top: 7px;
									background: url(../img/hero/jnword.png ) no-repeat 0 0;
								}
							}
						}
						.wuqi {
							z-index: 10;
							width: 205px;
							position: absolute;
							top: 375px;
							left: 490px;
							border: 1px solid red;
							img {
								display: inline-block;
								width: 66px;
								height: 66px;
								float: left;

							}
							span {
								/*padding:5px;*/
								padding-left: 5px;
								line-height: 52px;
								color: #e7e7e7;
								font-size: 16px;
								display: inline-block;
								width: 133px;
								height: 52px;
								float: left;
								margin-top: 7px;
								background: url(../img/hero/jnword.png ) no-repeat 0 0;
							}
						}
					}
				}

			}
		}
		.foot {
			top: 1100px;
		}
		.coor {
			top: 910px;
		}

	}
