.list {
	width: 1920px;
	min-width: 1005px;

	background: #101010 url(../img/list/bg.jpg) no-repeat top center;
	.wrap {
		width: 1005px;
		margin-left: -501px;
		position: absolute;
		left: 50%;
		.news {
			width: 998px;
			height: 783px;
			background: url(../img/list/newsbg.jpg) no-repeat 0 0;
			position: absolute;
			top: 600px;
			left: 8px;
			.title {
				width: 90%;
				height: 72px;
				/*border: 1px solid red;*/
				overflow: hidden;
				margin-bottom: 10px;
				li {
					padding-left: 45px;
					padding-right: 49px;
					/*border: 1px solid red;*/
					float: left;
					line-height: 60px;
					height: 72px;
					a {
						color: #fafabc;
						font-size: 18px;
						font-weight: normal;
					}

				}
				li.cur {
					background: url(../img/list/hover.jpg) no-repeat 50% 50%;
				}
			}

			.content {
				font-size: 16px;
				color: #888;
				li {
					overflow: hidden;
					line-height: 35px;
					width: 95%;
					margin-left: 10px;
					margin-right: 10px;
					border-bottom: 1px solid #999;
					a {
						color: #888;
						font-size: 16px;
						margin-left: 20px;
						float: left;
					}
					span {
						float: right;
						margin-right: 20px;
					}
				}
			}


		}
		.foot {
			top: 1630px;
		}
		.coor {
			top: 1430px;
		}
